<template>
  <div>
    <!-- 顶部标签 -->
    <van-nav-bar title="支付订单" @click-left="Return" class="bgc">
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>

    <div class="content">
      <div class="price">
        <p>
          <span style="font-size:12px;color:#ccc">支付金额</span>
        </p>
        <p>
          <span style="font-size:24px">￥</span>
          <span style="font-size:46px">{{ this.total}}</span>
        </p>
      </div>
      <!-- 支付方式 -->
      <div class="mode">
        <van-radio-group v-model="index">
          <van-cell-group>
            <van-cell
              v-for="(item,index) in payMades"
              :key="index"
              :class="[,'iconfont', item.icon]"
              :title="item.name"
              clickable
            >
              <template #right-icon>
                <van-radio checked-color="#2c1907" :name="index" />
              </template>
            </van-cell>
          </van-cell-group>
        </van-radio-group>
      </div>
      <van-button block color="linear-gradient(to right, #2c1907, #130101)" @click="payAll">确认支付</van-button>
    </div>
  </div>
</template>

<script>
import "../assets/css/order.less";

export default {
  data() {
    return {
      aid: "",
      sids: [],
      // 选择的索引值
      index: 1,

      // 商品数据
      productes: [],

      // 地址数据
      ressData: {},

      // 支付方式
      payMades: [
        {
          name: "支付宝",
          icon: "icon-zhifubao"
        },
        {
          name: "微信",
          icon: "icon-zhifu"
        },
        {
          name: "银行卡支付",
          icon: "icon-yinhang"
        }
      ],

      // 总金额
      total: ""
    };
  },
  created() {
    this.aid = this.$route.params.aid;
    this.sids = this.$route.params.sids;

    this.getInitData();
  },
  methods: {
    // 数据获取
    getInitData() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      // 地址数据获取
      this.axios({
        method: "GET",
        url: "/findAddressByAid",
        params: {
          appkey: this.appkey,
          tokenString,
          aid: this.aid
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 40000) {
            this.ressData = res.data.result[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
        });

      // 商品数据获取
      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids)
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 50000) {
            this.productes = res.data.result;
            this.sum(this.productes);
            this.total = this.total.toFixed(2);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    payAll() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      let address =
        this.ressData.province +
        this.ressData.city +
        this.ressData.county +
        this.ressData.addressDetail;
      let customer = {
        phone: this.ressData.tel,
        address: address,
        receiver: this.ressData.name
      };
      this.$toast("支付成功");

      this.axios({
        method: "POST",
        url: "/pay",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
          customer
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == 60000) {
            if (this.index >= 0) {
              // 设置查询状态
              this.productes.forEach(v => {
                v.status = 1;
              });

              let sids = this.sids;
              let obj = {
                name: "Myorder",
                params: { sids: this.sids }
              };
              this.$router.push(obj);
            } else {
              this.$toast("请选择支付方式");
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    // 计算总金额
    sum(v) {
      this.total = 0;
      v.forEach(v => {
        this.total += v.count * v.price;
      });
    },

    // 返回
    Return() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="less" scoped>
@import "../assets/iconfont/font/iconfont.css";
</style>